body {
  min-height: 100%;
  height: 100%;
  margin: 0 auto;
}
.form-wrapper {
  position: absolute;
  width: 300px;
  height: 300px;
  right: 3%;
  top: 55%;
}
.img {
  transform: translate(-50%);
  background: #000;
}
.video-wrapper {
  position: relative;
  min-width: 1200px;
  height: 100%;
}
.video-wrapper .video-box {
  z-index: -100;
  font-size: 0;
  width: 100%;
}
.video-wrapper .video-box video {
  width: 100%;
}
.video-wrapper .mask {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  z-index: 100;
}
.video-wrapper .mask-content {
  position: absolute;
  top: 45%;
  width: 100%;
  text-align: center;
}
.video-wrapper .mask-content h1 {
  font-size: 70px;
  color: #fff;
}
.video-wrapper .mask-content p {
  color: #fff;
  font-size: 28px;
  max-width: 700px;
  margin: 0 auto;
}
.img-box {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  color: red;
  height: 500px;
}
.world-map {
  background-color: #7e318e;
  padding-bottom: 47px;
}
.world-map .map-pic {
  margin: 0 auto;
  width: 1130px;
  height: 625px;
}
.world-map .world-data {
  display: inline-block;
  position: absolute;
  top: 45%;
  color: #fff462;
  text-align: center;
}
.world-map .world-data .left {
  margin-left: 140px;
}
.world-map .world-data .left #num1 {
  font-size: 80px;
}
.world-map .world-data .middle {
  margin-left: 280px;
}
.world-map .world-data .middle #num2 {
  font-size: 80px;
}
.world-map .world-data .right {
  margin-left: 290px;
}
.world-map .world-data .right #num3 {
  font-size: 80px;
}
.world-map .world-data p {
  margin-top: 10px;
  display: inline-block;
  font-size: 18px;
  color: #fff;
  text-align: center;
}
.world-map .world-data span {
  font-size: 30px;
  margin-top: 30px;
}
.course-video {
  margin-top: -8px;
}
.course-video .bottom {
  padding-bottom: 4px;
}
.course-video .video-item {
  height: 250px;
  width: 444px;
  margin-bottom: 115px;
}
.course-video .video-item.bottom {
  margin-bottom: 139px;
}
.course-video .video-item:nth-child(n+2) {
  margin-left: 87px;
}
.course-video .video-item p {
  color: #727171;
  font-size: 21px;
  margin-top: 27px;
}
.honor {
  background-color: #7e318e;
}
.honor .swiper-index {
  padding-bottom: 120px;
}
.honor .swiper-index .swiper-container {
  padding-bottom: 53px;
}
.honor .swiper-index .swiper-wrapper {
  margin-left: 70px;
  font-size: 21px;
}
.honor .swiper-index .swiper-pagination-bullets {
  bottom: -5px;
}
.honor .swiper-index .swiper-button-prev {
  background-image: url('../images/arrow_left.png');
  width: 50px;
  height: 50px;
  background-size: 50px 50px;
  margin-top: -60px;
}
.honor .swiper-index .swiper-button-next {
  background-image: url('../images/arrow_right.png');
  width: 50px;
  height: 50px;
  background-size: 50px 50px;
  margin-top: -60px;
}
.honor .swiper-index .swiper-slide {
  text-align: center;
  color: #fff;
  height: 100%;
  margin-top: 75px;
  margin-left: 20px;
}
.honor .swiper-index .swiper-slide .slide-item:nth-child(n+2) {
  margin-left: 85px;
}
.honor .swiper-index .swiper-slide img {
  width: 256px;
  height: 256px;
  margin-bottom: 50px;
}
.stars-say .content-box {
  margin-bottom: 75px;
}
.stars-say .star-left {
  width: 596px;
  height: 676px;
}
.stars-say .star-left .star-desc {
  position: absolute;
  top: 521px;
  left: 0;
  width: 596px;
  height: 155px;
  background-color: rgba(255, 255, 255, 0.2);
  color: #fff;
  padding: 15px;
}
.stars-say .star-left .star-desc span {
  font-weight: bold;
}
.stars-say .star-right {
  margin-left: 14px;
  width: 896px;
  height: 676px;
}
.stars-say .star-right img {
  height: 100%;
  width: 100%;
}
